import React, { Component } from "react"
import { Link } from "gatsby"
import Layout from "../components/GatsbyDefault/layout"
import SEO from "../components/GatsbyDefault/seo"

export default class IndexPage extends Component {
  render() {
    return (
      <Layout>
        <SEO title="Home" />
        <button onClick={() => window.navigator.clipboard.writeText("Test").then(() => console.log("Ok")).catch(ex => console.log("fail"))}>Test</button>
        <Link to="/player/">Go to Player Search</Link>
      </Layout>
    )
  }
}
